import { ComponentFixture, TestBed } from '@angular/core/testing';
import { Router } from '@angular/router';
import { Navbar } from './navbar';

// Test suite for the Navbar component
describe('Navbar', () => {
  let component: Navbar;
  let fixture: ComponentFixture<Navbar>;
  const routerSpy = jasmine.createSpyObj('Router', ['navigate']);

  beforeEach(async () => {
    await TestBed.configureTestingModule({
      declarations: [Navbar],
      providers: [
        { provide: Router, useValue: routerSpy }
      ]
    }).compileComponents();

    fixture = TestBed.createComponent(Navbar);
    component = fixture.componentInstance;
    fixture.detectChanges();
  });

  it('should create the component', () => {
    expect(component).toBeTruthy();
  });

  it('should navigate to the given path when clickEvent is called', () => {
    const testPath = '/dashboard';
    component.clickEvent(testPath);
    expect(routerSpy.navigate).toHaveBeenCalledWith([testPath]);
  });

  it('should throw an error when navigateTo is called', () => {

    expect(() => {component.navigateTo();}).toThrowError('Method not implemented.');
  });
});